html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-phone {
  width: 1em;
  height: 1em;
  fill: initial;
}

.icon-play {
  width: 1em;
  height: 1em;
  fill: #4e4e50;
}

.icon-rocket {
  width: 1em;
  height: 1em;
  fill: initial;
}

body {
  background: #FFF;
  font-size: 50px;
  font-size: calc(16px + (50 - 16) * ((100vw - 600px) / (1000 - 600)));
  line-height: 1.4;
}

@media (max-width: 600px) {
  body {
    font-size: 16px;
  }
}

@media (min-width: 1000px) {
  body {
    font-size: 50px;
  }
}

.block {
  display: flex;
}

* {
  font-size: 14px;
  font-family: sans-serif;
}

.wrap {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.container {
  max-width: 1000px;
  margin: 0 auto;
}

@media only screen and (max-width: 1020px) {
  .container {
    margin: 0 20px;
  }
}

.call, .btn {
  padding: 15px;
  display: flex;
  flex-direction: row;
  justify-contnet: spase-between;
  align-items: center;
  color: #fff;
  background: #54cfee;
  text-decoration: none;
  border-radius: 5px;
}

.call__img, .btn__img {
  padding-right: 14px;
  margin-right: 15px;
  position: relative;
}

.call__img .icon, .btn__img .icon {
  fill: #fff;
  font-size: 24px;
}

.header {
  margin-top: 20px;
  margin-bottom: 15px;
}

.header .container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.menu li {
  display: inline-block;
}

.menu li a {
  display: block;
  font-size: 12px;
  font-weight: bold;
  line-height: 20px;
  position: relative;
  color: #282F37;
  margin: 15px;
  margin-left: 20px;
  text-decoration: none;
}

.call__text {
  text-align: right;
}

.call__number {
  text-align: right;
  font-size: 24px;
  line-height: 1;
}

.call__phone {
  position: relative;
}

.call__phone:after {
  height: 100%;
  width: 1px;
  background: #fff;
  content: '';
  position: absolute;
  left: -15px;
  top: 0;
}

.menu li a {
  transition: all 1s ease-in-out;
}

.menu li a svg {
  position: absolute;
  left: 0;
  top: 0;
  margin: 15px;
  height: 25px;
  width: 110%;
  /* Swiggle Box */
}

.menu li a svg {
  stroke-dasharray: 265, 0;
  transition: all 1s ease-in-out;
  stroke: #4274D3;
  fill: none;
  stroke-width: 1;
}

.menu li a:hover svg path {
  stroke-width: 3;
  stroke-dasharray: 0, 350;
  stroke-dashoffset: 20;
  -transition: all 1s ease-in-out;
}

.hero {
  height: 100%;
  padding-top: 55px;
}

.hero .container {
  max-width: 700px;
  margin: 0 auto;
}

.hero__title {
  text-align: center;
  font-size: 45px;
  color: #282f37;
  line-height: 1.4;
  margin-bottom: 40px;
  font-weight: bold;
}

.hero__btns {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  position: relative;
}

.hero__or {
  color: #666666;
  font-weight: bold;
  font-size: 18px;
}

.basicBox {
  width: 45%;
  line-height: 1;
}

.btn__text {
  font-size: 22px;
  font-weight: bold;
}

.btn__img:after {
  height: 100%;
  width: 1px;
  background: #fff;
  content: '';
  position: absolute;
  right: 0;
  top: 0;
}

.btn-left {
  background: #ff9700;
}

.btn-right {
  background: #fff;
  color: #666666;
}

.btn-right.btn .icon {
  fill: #666666;
}

.btn-right.btn .btn__img:after {
  background: #666666;
}

.btn__text {
  margin-left: 10px;
}

.btn__img {
  position: relative;
}

.btn__img .icon {
  font-size: 30px;
}
