@import "helpers/all";
@import "generated/sprite-svg";
@import "common";
@import "layouts";
@import "main";

//////////////////////////////////////////////////////////////////////
//////////////////////////////////////////////////////////////////////
////////////////	MAIN    //////////////////////////////////////////
//////////////////////////////////////////////////////////////////////
//////////////////////////////////////////////////////////////////////
* {
	font-size: 14px;
	font-family: sans-serif; }
.wrap {
	display: flex;
	flex-direction: column;
	height: 100vh; }

.container {
	max-width: 1000px;
	margin: 0 auto;
	@include r(1020) {
		margin: 0 20px; } }

.call, .btn {
	padding: 15px;
	display: flex;
	flex-direction: row;
	justify-contnet: spase-between;
	align-items: center;
	color: #fff;
	background: #54cfee;
	text-decoration: none;
	border-radius: 5px;
	&__img {
		padding-right: 14px;
		margin-right: 15px;
		position: relative;
		.icon {
			fill: #fff;
			font-size: 24px; } } }

//////////////////////////////////////////////////////////////////////
//////////////////////////////////////////////////////////////////////
////////////////	HEADER  //////////////////////////////////////////
//////////////////////////////////////////////////////////////////////
//////////////////////////////////////////////////////////////////////

.header {
	margin-top: 20px;
	margin-bottom: 15px;
	.container {
		display: flex;
		align-items: center;
		justify-content: space-between; } }
.menu {
	li {
		display: inline-block;
		a {
			display: block;
			font-size: 12px;
			font-weight: bold;
			line-height: 20px;
			position: relative;
			color: #282F37;
			margin: 15px;
			margin-left: 20px;
			text-decoration: none; } } }


.call {
	&__text {
		text-align: right; }
	&__number {
		text-align: right;
		font-size: 24px;
		line-height: 1; }
	&__phone {
		position: relative;
		&:after {
			height: 100%;
			width: 1px;
			background: #fff;
			content: '';
			position: absolute;
			left: -15px;
			top: 0; } } }
////////////////////////////////////////////////////////////////////////////////////////
// Animation MENU
////////////////////////////////////////////////////////////////////////////////////////
.menu {
	li {
		a {
			transition: all 1s ease-in-out;
			svg {
				position: absolute;
				left: 0;
				top: 0;
				margin: 15px;
				// transform: translate(-50% 0)
				height: 25px;
				width: 110%;


				/* Swiggle Box */ }
			svg {
				path {}
				stroke-dasharray: 265, 0;
				transition: all 1s ease-in-out;
				stroke: #4274D3;
				fill: none;
				stroke-width: 1; }

			&:hover svg path {
				stroke-width: 3;
				stroke-dasharray: 0, 350;
				stroke-dashoffset: 20;
				-transition: all 1s ease-in-out; } } } }




//////////////////////////////////////////////////////////////////////
//////////////////////////////////////////////////////////////////////
////////////////	HERO    //////////////////////////////////////////
//////////////////////////////////////////////////////////////////////
//////////////////////////////////////////////////////////////////////
.hero {
	height: 100%;
	padding-top: 55px;
	.container {
		max-width: 700px;
		margin: 0 auto; }
	&__title {
		text-align: center;
		font-size: 45px;
		color: #282f37;
		line-height: 1.4;
		margin-bottom: 40px;
		font-weight: bold; }
	&__btns {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		align-items: center;
		width: 100%;
		position: relative; }
	&__or {
		color: #666666;
		font-weight: bold;
		font-size: 18px; } }
.basicBox {
	width: 45%;
	line-height: 1; }
.btn {
	// width: 45%
	// line-height: 1 ---if we don't have .basicBox
	&__text {
		font-size: 22px;
		font-weight: bold; }
	&__img {
		&:after {
			height: 100%;
			width: 1px;
			background: #fff;
			content: '';
			position: absolute;
			right: 0;
			top: 0; } } }


.btn-left {
	background: #ff9700; }
.btn-right {
	background: #fff;
	color: #666666; }
.btn-right.btn {
	.icon {
		fill: #666666; }
	.btn__img:after {
		background: #666666; } }

////////////////////////////////////////////////////////////////////////////////////////
// Animation BUTTON HERO
////////////////////////////////////////////////////////////////////////////////////////

.btn {
	&__text {
		margin-left: 10px; }
	&__img {
		position: relative;
		.icon {
			font-size: 30px; } }
	.rocket {
		.icon {} } }


